// Form
// ––––––––––––––––––––––––––––––––––––––––––––––––––

form {
  font-size: 1.6em;
  margin-bottom: 0rem;
  display: inline-block;
}

form .email {
}

form input {
  margin-bottom: 0rem;
}

input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="url"],
textarea,
select {
  width: 40rem;
  appearance: none; // Removes awkward default styles on some inputs for iOS
  background-color: transparent;
  border: 0.1rem solid #313131;
  border-radius: 0.4rem;
  box-shadow: none;
  box-sizing: inherit; // Forced to replace inherit values of the normalize.css
  height: 3.8rem;
  padding: 0.6rem 1rem; // The .6rem vertically centers text on FF, ignored by Webkit
  font-size: 1.2rem;
  display: inline-block;
  color: #fafafa;

  &:focus {
    border-color: $color-primary;
    outline: 0;
  }
}

select {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="14" viewBox="0 0 29 14" width="29"><path fill="#d1d1d1" d="M9.37727 3.625l5.08154 6.93523L19.54036 3.625"/></svg>')
    center right no-repeat;
  padding-right: 3rem;

  &:focus {
    background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="14" viewBox="0 0 29 14" width="29"><path fill="#9b4dca" d="M9.37727 3.625l5.08154 6.93523L19.54036 3.625"/></svg>');
  }
}

textarea {
  min-height: 6.5rem;
}

label,
legend {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

fieldset {
  border-width: 0;
  padding: 0;
}

input[type="checkbox"],
input[type="radio"] {
  display: inline;
}

.label-inline {
  display: inline-block;
  font-weight: normal;
  margin-left: 0.5rem;
}
